h1 {
  font-size: 68px;
  margin-bottom: 50px;
  line-height: 80px;
  font-weight: $fontweight-black;
  color: $color-navy;
}

h2 {
  font-size: 40px;
  line-height: 56px;
}

h3 {
  font-size: 30px;
  line-height: 40px;
}

h4 {
  font-size: 25px;
  line-height: 40px;
}

h2,
h3,
h4 {
  font-weight: $fontweight-black;
  color: $color-navy;
  margin-bottom: 32px;
  margin-top: 70px;
}


p {
  margin: $baseParagraphMargin 0;
  color: $color-navy;
  font-size: 24px;
  font-weight: $fontweight-light;
  line-height: 42px;
}

a {
  color: $color-red;
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.paragraphType-image-caption {
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    border-radius: 10px;
  }

  .fieldName-field-caption {
    color: $color-grey;
    font-size: 16px;
    line-height: 27px;
  }
}

blockquote p {
  color: $color-navy;
  font-size: 32px;
  line-height: 50px;
}

b,
strong {
  font-weight: $fontweight-black;
}


em {
  font-style: oblique;
}


i {
  font-style: italic;
}


small {
  font-size: 0.8em;
}


big {
  font-size: 1.2em;
}


dl dt {
  font-weight: $fontweight-black;
}


abbr {
  text-transform: uppercase;
}

.caption > figcaption {
  color: $color-grey;
  font-size: 16px;
  line-height: 27px;
  text-align: center;
}

.node-content ul li {
  list-style: none;
  color: $color-navy;
  font-size: 24px;
  font-weight: 300;
  line-height: 42px;

  &:before {
    color: $color-red;
    content: '\2022';
    font-size: 60px;
    padding-right: 10px;
    position: relative;
    top: 15px;
  }
}

.node-content ol li {
  color: $color-navy;
  font-size: 24px;
  font-weight: 300;
  line-height: 42px;
}
