html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  background-color: $color-background;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin-top: 79px;
  line-height: 1.75;
  font-family: $font-oswald;
  color: $color-text;
  font-weight: $fontweight-light;
  margin: 0;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  background-color: $color-background;
}
