// sass settings and variables
$width-xs: 330px;
$width-s: 600px;
$width-m: 800px;
$width-l: 1100px;
$width-xl: 1400px;
$width-max: 1600px;

// fonts
@import url('https://fonts.googleapis.com/css?family=Merriweather:300,400,700,900|Oswald:300,400,500,600');
$font-oswald: 'Oswald', sans-serif;
$font-merriWeather: 'Merriweather', sans-serif;

$fontweight-light: 300;
$fontweight-regular: 400;
$fontweight-bold: 500;
$fontweight-black: 600;

// raw colors
$color-white: #fff;
$color-black: #000;
$color-charcoal: #333;
$color-red: #cb6b4f;
$color-darkred: #b3573c;
$color-navy: #282561;
$color-lightGrey: #e6e6e6;
$color-grey: #4d4d4d;
$color-yellow: #e8ed6a;
$color-orange: #ff8555;
$color-magenta: #c277d0;
$color-blue: #56aec6;
$color-darkblue: #438ea2;
$color-green: #4fcb78;
$color-purple: #8b87e3;
$color-alto: #c9c9c9;

// semantic colors
$color-background: $color-white;
$color-text: $color-navy;

// layout variables
$baseFontSize: 24px;
$baseParagraphMargin: 1.3333em;
$sidePaddingMax: 2rem;
$wideInnerWidth: 1188px;
$readingInnerWidth: 792px;
